@import url(https://fonts.googleapis.com/css2?family=Vollkorn:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Vollkorn:wght@800&display=swap);
body {
  margin: 0;
  font-family: 'Vollkorn', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fcfcf5;
}

code {
  font-family: 'Vollkorn', serif;
}

.topnav {
    overflow: hidden;
    margin: 70px 10% 80px 10%;
}

.topnav a {
    float: right;
    display: block;
    color: #155e96;
    text-align: center;
    padding: 15px 15px;
    text-decoration: none;
    font-size: 20px;
}

#logoheader {
    float: left;
    color: #f1785a;
}

#logoheader:hover {
    -webkit-text-decoration-line: overline;
            text-decoration-line: overline;
    -webkit-text-decoration-color: #155e96;
            text-decoration-color: #155e96;
}

#logoheader2:hover {
    -webkit-text-decoration-line: overline;
            text-decoration-line: overline;
    -webkit-text-decoration-color: #155e96;
            text-decoration-color: #155e96;
}

.topnav a:hover {
    -webkit-text-decoration-line: overline;
            text-decoration-line: overline;
    -webkit-text-decoration-color: #f1785a;
            text-decoration-color: #f1785a;
}
 
@media (max-width: 600px) {
    .topnav {
        margin: 30px 5% 40px 5%;
    }
    .topnav a {
        padding: 3px 8px;
        font-size: 17px;
    }
}


  

.bottomnav {
    overflow: hidden;
    margin-top: 30px;
    background-color: #548ab3;
}

.bottomnav a {
    display: inline-block;
    padding: 10px 10px;
    margin: 30px 0px 30px 0px;
    font-size: 18px;
}

.bottomnav img {
    width: 40px;
}

#contacttext {
    font-size: 20px;
    color: #fcfcf5;
    -webkit-text-decoration-line: overline;
            text-decoration-line: overline;
    margin: 20px 0px -45px 0px;
}

.frontpicture {
    width: 80%;
    text-align: center;
    height: auto;
}

h1 {
    color: #f1785a;
    text-align: left;
    margin: 95px 10% 30px 10%;
}

#about {
    text-align: left;
    margin: 0px 10% 40px 10%;
    font-size: 20px;
    color: #155e96;
}

#sidebar {
    position: absolute;
    text-align: center;
    margin-left: 5%;
}

@media (max-width: 600px) {
    #about {
        font-size: 15px;
        margin: 0px 5% 80px 5%;
    }
    h1 {
        font-size: 17px;
        margin: 45px 5% 10px 5%;
    }
    .frontpicture {
        width: 100%;
    }
    #resume {
        text-align: center;
    }
}

#resume a {
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
    color: #f1785a;
    border-width: 2.5px;
    border-style: solid;
    border-color: #f1785a;
    border-radius: 10px;;
    padding: 1px 2px 1px 2px;
}

#skills {
    color: #f1785a;
}




.grid-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 0px 10% 0px 10%;
}

.grid-item img {
    width: 100%;
    border-radius: 10px;
    margin-bottom: 140px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.grid-item img:hover {
    opacity: 90%;
}

.title {
    color: #f1785a;
    font-size: 27px;
    float: left;
    margin-left: 50px;
    font-weight: bold;
}

.technologies {
    margin-top: 10px;
    color: #f1785a;
    font-size: 24px;
    float: left;
    margin-left: 50px;
    font-weight: bold;
}

  #recent {
    text-align: center;
    margin: -30px 0px 50px 0px;
  }

  .description {
    font-size: 20px;
    margin-left:50px;
    text-align: left;
    color: #155e96;
  }

  #projectlinks a {
    float: left;
    -webkit-text-decoration: transparent;
            text-decoration: transparent;
    color: #f1785a;
    border-width: 2.5px;
    border-style: solid;
    font-size: 19px;
    border-color: #f1785a;
    border-radius: 10px;;
    padding: 1px 2px 1px 2px;
    margin-top: 10px;
    margin-right: 10px;
}

#projectlinks {
    margin-left: 50px;
}

@media (max-width: 1200px) {
    .description {
        font-size: 17px;
    }
    .title {
        font-size: 24px;
    }
    .technologies {
        font-size: 21px;
    }
    #projectlinks a {
        font-size: 17px;
    }
    .grid-container {
        margin: 0px 5% 0px 5%;
    }
}

@media (max-width: 1000px) {
    .description {
        font-size: 15px;
    }
}

@media (max-width: 800px) {
    .grid-container {
    margin: 0px 2% 0px 2%;
    }
    .description {
        margin-left: 25px;
    }
    .title {
        font-size: 22px;
        margin-left: 25px;
    }
    .technologies {
        font-size: 19px;
        margin-left: 25px;
    }
    #projectlinks {
        margin-left: 25px;
    }
    #projectlinks a {
        font-size: 15px;
    }
}

@media (max-width: 700px) {
    .grid-container {
        grid-template-columns: 1fr;
    }
    .grid-item img {
        border-radius: 10px;
        margin-bottom: 10px;
    }
    #projectlinks {
        margin-bottom: 150px;
        margin-left: 0px;
    }
    .grid-item {
        margin: 0px 5% 0px 5%;
    }
    .description {
        margin-left: 0px;
        margin-top: -8px;
    }
    .title {
        margin-left: 0px;
    }
    .technologies {
        margin-left: 0px;
    }
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

